.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

/* App footer */
.app-footer {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  background: #cbdbef;
  color: #2300007f;
  font-size: 12px;
  line-height: 1;
  z-index: 100;
}

.app-footer .footer-accent {
  height: 1px; /* 10px bar */
  background: #ff6a00; /* accent color */
}

.app-footer .footer-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  gap: 8px;
  padding: 8px 12px;
}

.app-footer .footer-left {
  display: flex;
  align-items: center;
  gap: 0.3em; /* approximate a single word space */
  flex-wrap: wrap;
}

.app-footer .footer-right {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  gap: 0;
  text-align: right;
}

.app-footer .footer-left,
.app-footer .footer-right {
  min-width: 0; /* allow wrapping within each half */
}

.app-footer .footer-left a {
  white-space: nowrap; /* keep the two-word brand together */
}

@media (max-width: 640px) {
  .app-footer .footer-right {
    flex-basis: 100%;
    margin-left: 0;
    justify-content: flex-end;
  }
}

.app-footer a {
  color: #2300007f;
  text-decoration: none;
}

.app-footer a:hover,
.app-footer a:focus {
  text-decoration: underline;
}

@keyframes pop-in {
  0% {
    height: 1px;
    width: 1px;
    transform: translate(-0.5, -0.5);
    opacity: 0;
    left: -0.5px;
    top: -0.5px;
  }
  50% {
    height: 50px;
    width: 50px;
    transform: translate(-25, -25);
    opacity: 0.5;
    left: -25px;
    top: -25px;
  }
  100% {
    height: 10px;
    width: 10px;
    transform: translate(-2.5, -2.5);
    opacity: 1;
    left: -5px;
    top: -5px;
  }
}

.single-point {
  animation: pop-in 5s ease-out;
  left: -5px;
  top: -5px;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.single-point-annotation {
  opacity: 0;
  animation-name: fadeIn;
  animation-duration: 2s;
  animation-fill-mode: forwards;
}

path {
  stroke-linejoin: round !important;
}
