body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.grid-container {
  display: grid;
  grid-template-columns: 33% auto;
  gap: 10px; /* Optional: Adds some space between the grid items */
  height: 100vh; /* Set the height of the grid container to the full viewport height */
}

.grid-item {
  padding: 20px;
  border: 1px solid #ccc;
  text-align: center;
}

.item1 {
  background-color: #f0f0f0;
  height: 90vh; /* Make the left item the same height as the viewport */
  overflow-y: hidden;
}

.item2 {
  background-color: #e0e0e0;
  overflow-y: auto; /* Makes the right item scrollable if the content exceeds the viewport height */
}
